import {
  ChartComponent,
  MotifColor,
  MotifComponent,
  getBasePatternFromChart,
  getChartFromBasePattern,
  getMergedBasePattern,
  getMotifFilledChart,
  getSlicedBasePattern,
} from "@tanyoknits/shared";
import PageContentWrapper from "../../layout/PageContentWrapper";
import { PatternBySizeProps } from "../../layout/PatternBySize";
import { repeatedPattern } from "./motifs";
import { schemaBySize } from "./sizes";
import { getBasePatternByParts } from "../../patternMakers/cardigan";
import InteractivePattern from "../../components/InteractivePattern";

export default function Instruction({ menu, size }: PatternBySizeProps) {
  const repeat = repeatedPattern[size];
  const schema = schemaBySize[size];

  const { rows, yokeSplit, total, vNeck, arm } = schema;
  const { every4RowAfterRaglan } = vNeck;
  const { hemDecStart, lastNeckInc, split } = rows;
  const { body, sleeve } = total;
  const { frontOneSide, underarm } = yokeSplit;
  const { cuffBO } = arm;

  // Body part
  const fullBodyChart = getMotifFilledChart(repeat, body, hemDecStart, 3);

  const motifRepeatRow = repeat.length;
  const combinedMotif = {
    id: "grape" as MotifColor,
    stsCount: 4,
    rowCount: motifRepeatRow,
  };
  const bodyBasePattern = getBasePatternFromChart(
    fullBodyChart,
    null,
    0,
    combinedMotif
  );
  // parts
  const frontLeftBasePattern = getSlicedBasePattern(
    bodyBasePattern,
    [0, lastNeckInc],
    [0, frontOneSide + every4RowAfterRaglan]
  );
  const frontRightBasePattern = getSlicedBasePattern(
    bodyBasePattern,
    [0, lastNeckInc],
    [body - (frontOneSide + every4RowAfterRaglan), body]
  );
  const backBasePattern = getSlicedBasePattern(
    bodyBasePattern,
    [0, rows.split],
    [
      frontOneSide + every4RowAfterRaglan + underarm,
      body - (frontOneSide + every4RowAfterRaglan + underarm),
    ]
  );

  // Sleeve - include yoke & after split
  const fullSleeveChart = getMotifFilledChart(
    repeat,
    sleeve,
    split + cuffBO,
    3
  );
  // parts
  const sleeveBasePattern = getBasePatternFromChart(
    fullSleeveChart,
    null,
    0,
    combinedMotif
  );
  const yokeBasePattern = getSlicedBasePattern(
    sleeveBasePattern,
    [0, split],
    [underarm / 2, sleeve - underarm / 2]
  );

  // Total base patterns
  const basePatterns = getBasePatternByParts(
    frontLeftBasePattern,
    frontRightBasePattern,
    backBasePattern,
    yokeBasePattern,
    schema
  );

  const totalYoke = getMergedBasePattern(basePatterns, split - 1);

  return (
    <PageContentWrapper>
      {menu === 0 ? (
        <MotifComponent chart={repeat} stsCount={20} />
      ) : (
        <InteractivePattern
          chart={{
            chart: getChartFromBasePattern(totalYoke),
            size: "S",
            direction: "flat",
            showStsCountPerRow: true,
          }}
        />
      )}
    </PageContentWrapper>
  );
}
